import './content.css';
// 初始化侧边栏
document.addEventListener('DOMContentLoaded', function () {
    console.log('侧边栏初始化');
    // 添加关闭按钮事件监听
    var closeButton = document.querySelector('.linkedin-booster-close');
    if (closeButton) {
        closeButton.addEventListener('click', function () {
            console.log('关闭侧边栏');
            // 通知 background 脚本关闭侧边栏
            chrome.runtime.sendMessage({ action: 'closeSidePanel' });
        });
    }
    // 添加按钮事件监听
    var extractButton = document.getElementById('extract-candidates');
    var batchConnectButton = document.getElementById('batch-connect');
    if (extractButton) {
        extractButton.addEventListener('click', function () {
            console.log('提取候选人信息');
            // TODO: 实现提取候选人信息的逻辑
        });
    }
    if (batchConnectButton) {
        batchConnectButton.addEventListener('click', function () {
            console.log('批量发送连接请求');
            // TODO: 实现批量发送连接请求的逻辑
        });
    }
});
